import { regexList, cleanText } from '../../../helpers/publicationHelper'
import Carousel from '../../../components/caroussels/carousel'
import VignetteEvent from '../../../components/caroussels/vignetteEvent'




const SectionEvents = ({ data }) => {





    const classes = useStyles()
    const [isMounted, setIsMounted] = useState(false)

    useEffect(() => {

        if (!isMounted)
            setIsMounted(true)

    }, [])



    if (!data)
        return null

    const pageFormateur = data.pageFormateur  ? data.pageFormateur : data.originalPageFormateur

    const formateur = pageFormateur
    const publications = pageFormateur?.publications

    if (!publications)
        return null

    const now = Date.now() / 1000


    let eventList = null
    eventList = pageFormateur?.children
    const orderedList = eventList
        .filter(child => child.internal?.type === "DBEvent")
        .filter(child => child.state === "published" || child.state === "PREPUBLISHED")
        .filter(child => child.date > now)
        .sort((a, b) => a.date - b.date)

    const pastOrderedList = eventList
        .filter(child => child.internal?.type === "DBEvent")
        .filter(child => child.state === "published" || child.state === "PREPUBLISHED")
        .filter(child => child.date < now)
        .sort((a, b) => a.date - b.date)






    

    if (!(orderedList.length > 0 || pastOrderedList.length>0))
        return null


    
    

    return (
        <Box className={classes.componentContainer} >




            <Carousel isMounted={isMounted} cellTemplate={VignetteEvent} cellsData={orderedList} carouselConfig={carouselConfig} />
            <Carousel isMounted={isMounted} cellTemplate={VignetteEvent} cellsData={pastOrderedList} carouselConfig={carouselConfigPast} />

        </Box>)
}

export default SectionEvents



